export function registerShader(key, obj) {
  shader_data[key] = obj;
}

function getShader(key) {
  let elem = shader_data[key];
  if (!elem.shader) {
    if (elem.fp) {
      elem.shader = shaders.create(gl.FRAGMENT_SHADER, key, elem.fp);
    } else {
      elem.shader = shaders.create(gl.VERTEX_SHADER, key, elem.vp);
    }
  }
  return elem.shader;
}

